*{
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.MainContainer{
  max-width: 1000px;
  padding: 0 10px;
  margin: 0 auto;
}

.HeaderHolder{
  height: 50px;
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
}

.OrgName{
  color: blueviolet;
}

.ImageEngine{
  display: flex;
}

.GenImgContainer{

}

.ImageBox{
  position: relative;
  height: 500px;
  width: 500px;
  padding: 10px 10px;
  margin: 0 auto;
}

.LayeredImage{
  height: 500px;
  width: 500px;
  position: absolute;
}

.fabricSelectionTitle{
  padding-left: 10px;
  padding-top: 10px;
}

.fabricButton{
  border: none;
  cursor: pointer;
  margin: 10px 10px;
}

.fabricButtonImage{
  height: 55px;
  width: 43px;
}

.fabricSelection{
  display: flex;
  flex-direction: column;
}

.fabricSelector{
  display: flex;
  justify-content: space-between;
}

.sleevSelection{
  margin-top: 30px;
}

.sleevSelectionTitle{
  padding-left: 10px;
}

.sleevSelector{
  display: flex;
}

.sleevButton{
  background-color: #335566;
  color: white;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}

.buttonSelection{
  margin-top: 30px;
}

.buttonSelectionTitle{
  padding-left: 10px;
}

.buttonSelector{
  display: flex;
}

.buttonButton{
  padding: 10px;
  margin: 10px;
  background-color: #335566;
  color: white;
  cursor: pointer;
}